
export const creativeFormatSelect = () => {
  const radios = document.querySelectorAll('input[name="creative-format"]');
  radios.forEach(function(radio){
    radio.addEventListener('change', (evt) => {
      const creativeContents = Array.from(document.getElementById('creative-format-content').children);
      creativeContents.forEach(function(content) {
        console.log(content);
        content.style.display = 'none';
      });
      switch(evt.target.value) {
        case 'single-image':
          showContent('creative-format_single-image');
        break;
        case 'plain-text':
          showContent('creative-format_plain-text');
        break;
        case 'html':
          showContent('creative-format_html');
        break;
        case 'html-with-text':
          showContent('creative-format_plain-text');
          showContent('creative-format_html');
        break;
      }
    });
  })
}

const showContent = (contentId) => {
  document.getElementById(contentId).style.display = 'block';
}
